const React = require('react');
const Layout = require('./src/components/layout').default;
const { Provider } = require('@react-spectrum/provider');
const { darkTheme } = require('@react-spectrum/theme-dark');

module.exports = {
  wrapPageElement: ({ element, props }) => <Layout {...props}>{element}</Layout>,
  wrapRootElement: ({ element }) => <Provider theme={darkTheme}>{element}</Provider>
}